import React, { Component } from 'react';
import Slider from 'react-animated-slider';
import axios from '../../axios-connect';

import Button from '../UI/Button/Button';

import 'react-animated-slider/build/horizontal.css';
import classes from './Banner.module.scss';

class Banner extends Component {
    state = {
        isLoaded: false,
        banners: [],
        error: ''
    }

    componentDidMount() {
        this.loadBanners();
    }

    loadBanners = () => {
        axios.get('/banner')
            .then(response => {
                this.setState({
                    isLoaded: true,
                    banners: [...response.data]
                });
            })
            .catch(error => {
                this.setState({
                    isLoaded: false,
                    error: error.message
                });
            });
    }

    render() {
        let bannerBody = null;
        if (this.state.isLoaded && this.state.banners.length > 0) {
            bannerBody = (
                <Slider
                    autoplay={5000}
                    previousButton={null}
                    nextButton={null}
                >
                    {this.state.banners.map((item, index) => (
                        <div
                            key={index}
                            style={{ background: `url('${item.imgurl}') center top`, backgroundSize: "cover" }}
                        >
                            <div className={classes.center}>
                                <h1>{item.title}</h1>
                                <p>{item.body}</p>
                                <Button link={item.link}>Дэлгэрэнгүй</Button>
                            </div>
                        </div>
                    ))}
                </Slider>
            );
        } else if (this.state.error !== '') {
            bannerBody = (
                <div className={classes.center}>
                    <p>{this.state.error}</p>
                </div>
            );
        }

        return bannerBody;
    }
};

export default Banner;
