import React from 'react';

import Event from './Event/Event';
import classes from './Timeline.module.scss';

const events = [
    {
        year: '2006',
        items: [
            {
                brief: 'Event1: Lorem ipsum dolor sit amet consectetur adipisicing elit. Lorem ipsum dolor sit amet consectetur adipisicing elit.',
                imgurl: ''
            },
            {
                brief: 'Event2: Lorem ipsum dolor sit amet consectetur adipisicing elit.',
                imgurl: 'http://localhost:5000/static/img/vendor/devicelock.png'
            }
        ]
    },
    {
        year: '2007',
        items: [
            {
                brief: 'Event3: Lorem ipsum dolor sit amet consectetur adipisicing elit. Lorem ipsum dolor sit amet consectetur adipisicing elit. Lorem ipsum dolor sit amet consectetur adipisicing elit.',
                imgurl: 'http://localhost:5000/static/img/vendor/kaspersky.png'
            }
        ]
    },
    {
        year: '2008',
        items: [
            {
                brief: 'Event4: Lorem ipsum dolor sit amet consectetur adipisicing elit.',
                imgurl: ''
            },
            {
                brief: 'Event5: Lorem ipsum dolor sit amet consectetur adipisicing elit.',
                imgurl: ''
            },
            {
                brief: 'Event6: Lorem ipsum dolor sit amet consectetur adipisicing elit.',
                imgurl: 'http://localhost:5000/static/img/vendor/kaspersky.png'
            }
        ]
    },
    {
        year: '2009',
        items: [
            {
                brief: 'Event7: Lorem ipsum dolor sit amet consectetur adipisicing elit.',
                imgurl: ''
            },
            {
                brief: 'Event8: Lorem ipsum dolor sit amet consectetur adipisicing elit.',
                imgurl: ''
            }
        ]
    }
];

const timeline = (props) => {
    let content = [];

    events.forEach((item, index) => {
        if (index % 2 === 0) {
            content.push(
                <div className={classes.TimelineItem}>
                    <div className={classes.FirstItem}>
                        <div className={classes.FirstDot}></div>
                    </div>
                    <div className={classes.SecondItem}>
                        <Event event={item} txtAlign={'left'} />
                    </div>
                </div>
            );
        } else {
            content.push(
                <div className={classes.TimelineItem} >
                    <div className={classes.FirstItem}>
                        <Event event={item} txtAlign={'right'} />
                    </div>
                    <div className={classes.SecondItem}>
                        <div className={classes.SecondDot}></div>
                    </div>
                </div>
            );
        }
    });

    return (
        <div className={classes.Timeline}>
            <div className={classes.EmptyColLeft}></div>
            {content}
            <div className={classes.EmptyColRight}></div>
        </div>
    );
}

export default timeline;
